	import listCell from '@/components/mix-list-cell';
    import {  
        mapState, mapActions
    } from 'vuex';  
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				viewList: [],
				appVersion: 0
			}
		},
		onLoad(){
			this.init()





		},




















        computed: {
			...mapState({
				userinfo: state => state.user.userinfo
			}),
			hasLogin() {
				return this.userinfo.id ? true : false
			}
		},
        methods: {
			...mapActions(['logout']),
			init(){
				return new Promise ((resolve, reject) => {
					Promise.all([this.getViewList()]).then(res => {
						resolve(res)
					}).catch(e => { reject(e) })
				})
			},
			getViewList() {
				this.$http.post('user.viewlist').then(res => {
					this.viewList = res.data.data
				})
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url){
				if(!this.userinfo.id){
					url = '/pages/public/login';
				}
				uni.navigateTo({  
					url
				})  
			}, 
			navPlugin(addon, url) {
				if (this.$tools.has_addon(addon)) this.navTo(url)
			},
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				/* if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY; */
			},
			coverTouchmove(e){
				/* moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				} */
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			},
			toLogout(){
				uni.showModal({
				    content: '确定要退出登录么',
				    success: (e)=>{
				    	if(e.confirm){
				    		this.logout().then(() => {
								this.viewList = []
								uni.navigateBack()
							})
				    	}
				    }
				});
			},
			toLogin() {
				uni.navigateTo({
					url: '/pages/public/login'
				})
			},
			checkVersion() {
				let _this = this
				plus.runtime.getProperty(plus.runtime.appid, function(widgetInfo) {
					let platform = uni.getSystemInfoSync().platform
					_this.$http.post('update', {version: widgetInfo.version, name: widgetInfo.name, platform}).then(res => {
						var data = res.data
						if (data.update == 1) {
							uni.showModal({
								title: '检测到更新，是否下载？',
								content: data.description,
								confirmText: '确定',
								confirmColor: '#FF4443',
								cancelText: "取消",
								cancelColor: '#A9A9A9', 
								success: e => {
									if (e.confirm) {
										uni.showLoading({
											icon: 'none'
										})
										uni.downloadFile({
											url: data.wgtUrl,
											success: function(downloadResult) {
												if (downloadResult.statusCode == 200) {
													plus.runtime.install(downloadResult.tempFilePath, {
														force: false
													}, function() {
														uni.hideLoading()
														plus.runtime.restart()
													}, function(e) {
														uni.hideLoading()
														uni.showModal({
															title: '提示',
															content: e.message
														})
													})
												}
											}
										})
										
									}
								}
							})
						} else {
							uni.showToast({
								title: "当前是最新版本"
							})
						}
					}).catch(e => {})
				});
			},
		},
		onShow() {
			this.getViewList()
		},
		onPullDownRefresh() {
			this.init().then(e => {
				uni.stopPullDownRefresh()
			}).catch(e => {
				uni.stopPullDownRefresh()
			})
		}
    }  
